import React,  { useState , useEffect} from 'react';
import ReactDOM from 'react-dom';
import './index.css';

const  stepList = [
  "Realization",
  "Thirst",
  "Comprehension",
  "Action",
  "Perserverance",
  "Ascension",
  "Ilumination",
];



function Hello({library, message, number, lake, login})  {
 const [ checked, setchecked] = useState(false);
 const  [status, setstatus] = useState("Open");
 const  [ data, setdata] = useState(null);


 useEffect( () => {
   fetch(`https://api.github.com/users/${login}`)
   .then(res => res.json())
   .then(setdata)
   .catch(console.error)
   
 }, []);

  return (
    <div>
        { data ? 
        (<>
        <div>
        <h4 id ='header'>{data.login}</h4>
         <img src ={data.avatar_url}  width={60}/>
         </div>
         </>
          ) :
        (<h1></h1>)
      }
    <h1 id ='heading'>Journey Into the Deep {library} </h1>
    <h2> {message} Into the {number} Dimensions</h2>

    <ul>
      {lake.map(lake => <li key={lake.toString()}> {lake} 
    <input type="checkbox"  value = {checked} onChange= {()=> setchecked( checked => !checked)}/>
      { checked ? "checked": "not checked"}  
           </li>)}
    </ul>

    <h4> click here to <button onClick = {()=> setstatus("Welcome")}>{status} </button></h4>
    

    </div>
  )
}

function App (props){
  

  return(
    <>
    {props.user === "exist" ? ( <Hello library="Horizon" message="Free your mind and soar" number ={7} lake = {stepList} login ="ManlikeTeetos" /> ) :
     ( <Hello library="Horizon" message="Welcome to a new way of Life by going " number ={7} lake = {stepList}  login ="ManlikeTeetos"/>) }
    </>
   
    
  );

    
    
  
}

ReactDOM.render( 
  <App user ="new" />,
  document.getElementById('root')
);


